.header {
    height: 15vh;
    background-color: rgb(179, 209, 255);
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header-button {
    background-color: white;
    border-radius: 20px;
    border: none;
    font-size: 16px;
    padding: 10px 15px;
    color: rgb(49, 48, 49);

    &:hover {
        transform: translate(0px, -2px);
        box-shadow: 3px 5px 5px rgb(78, 78, 78);
    }

    &:active {
        transform: translate(0px, -1px);
        box-shadow: 2px 3px 3px rgb(78, 78, 78);;
    }
}